.componentMain {
  padding: 32px 42px 64px 42px;
  background-color: var(--backgroundColor);

  @media screen and (max-width: 768px) {
    padding: 32px 16px 32px 16px;
  }

  &__header {
    color: var(--grey-4);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px;
  }

  &__examples {
    padding-top: 58px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 16px;
    &_single {
      justify-content: center;
    }
    &_halfWidth > div {
      min-width: 50%;
    }
  }

  &__tabsContainer {
    margin-top: 64px;
  }

  &__code,
  &__code * {
    overflow: hidden;
    @media screen and (max-width: 768px) {
      overflow-x: scroll;
      max-width: calc(100vw - 32px);
    }
  }
}
